body {
  background: #FFF;
  font-family: "CarlsbergSansW05-Light", "Helvetica", Arial, sans-serif;
  width: 100%;
  min-width: 320px;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #636363;
  font-size: 16px;
  line-height: 150%;
}
body.body-welcome {
  padding: 20px;
  background: #ccc;
  display: flex;
  justify-content: center;
  align-items: center;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

img {
  display: inline-block;
  max-width: 100%;
  height: auto;
}

svg {
  max-width: 100%;
  max-height: 100%;
}

picture {
  display: block;
}

.background-image {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}
.background-image picture {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.background-image picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.container {
  width: 100%;
  max-width: 1252px;
  padding: 0 20px;
  margin: 0 auto;
}

.wrapper {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

@media screen and (min-width: 1921px) {
  body {
    background: #ccc;
  }

  .wrapper {
    background: #fff;
  }
}
@media screen and (max-width: 767px) {
  body.body-welcome {
    padding: 0;
  }
}
.btn {
  width: 100%;
  min-height: 70px;
  font-family: CarlsbergSansW05-Bold;
  font-size: 20px;
  line-height: 160%;
  background: #D1112E;
  color: #fff;
  border-radius: 49px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border: 2px solid transparent;
  transition: all 0.2s ease;
}
.btn:hover, .btn:focus {
  background: transparent;
  color: #D1112E;
  border-color: #D1112E;
}
.btn--outline {
  background: transparent;
  border: 2px solid #D1112E;
  color: #D1112E;
}
.btn--outline:hover, .btn--outline:focus {
  background: #D1112E;
  color: #fff;
}

.header__inner {
  color: #fff;
}
.header__top {
  padding: 40px 20px 45px;
  display: flex;
  flex-direction: column;
  background: #7F7F7F;
}
.header__logo {
  display: inline-block;
  margin-bottom: 40px;
  align-self: flex-end;
}
.header__title {
  margin-bottom: 24px;
  font-family: CarlsbergSansW05-Black;
  font-size: 35px;
  line-height: 120%;
  text-transform: uppercase;
}
.header__text {
  font-family: CarlsbergSansW05-Bold;
  font-size: 23px;
  line-height: 140%;
  text-transform: uppercase;
}
.header__img {
  position: relative;
  top: -2px;
}
.header__img img {
  width: 100%;
  display: block;
  position: relative;
  bottom: -1px;
}
.header__bottom {
  display: none;
}

@media screen and (min-width: 1024px) {
  .header {
    padding-bottom: 100px;
  }
  .header__inner {
    padding-bottom: 50px;
    position: relative;
  }
  .header__top {
    width: 100%;
    max-width: 450px;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 5;
    background: transparent;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  .header__title {
    text-align: right;
  }
  .header__text {
    text-align: right;
  }
  .header__bottom {
    display: block;
    height: 250px;
    position: absolute;
    bottom: -120px;
  }
}
@media screen and (min-width: 1280px) {
  .header__top {
    max-width: 570px;
  }
  .header__logo img {
    width: 194px;
  }
  .header__title {
    margin-bottom: 30px;
    font-size: 65px;
  }
  .header__text {
    font-size: 28px;
  }
}
@media screen and (min-width: 1366px) {
  .header__top {
    right: 5%;
  }
}
@media screen and (min-width: 1800px) {
  .header__top {
    right: 16.7%;
    top: 0;
  }
  .header__logo {
    margin-bottom: 120px;
  }
  .header__title {
    margin-bottom: 42px;
  }
  .header__text {
    font-size: 32px;
  }
  .header__bottom {
    height: 280px;
  }
}
.scooter__main {
  width: 272px;
  height: 272px;
  margin: 0 auto;
  margin-bottom: 54px;
  border-radius: 50%;
  background: #C4C4C4;
  position: relative;
}
.scooter__logo {
  position: absolute;
  top: 136px;
  right: 21px;
}
.scooter__img {
  max-width: none;
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 5;
}
.scooter__img-bl {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transform: translate(-10%, -20%);
}
.scooter__img-bl img {
  max-width: none;
}
.scooter__img--active {
  display: block;
}
.scooter__manage {
  width: 100%;
  max-width: 330px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  position: relative;
  z-index: 10;
}
.scooter__btn {
  display: block;
  width: 60px;
  height: 60px;
  margin-right: calc((100% - 240px)/3);
  border-radius: 50%;
  background: #C4C4C4;
  border: 1px solid transparent;
}
.scooter__btn--active {
  border-color: #D1112E;
}
.scooter__btn:nth-child(4n) {
  margin-right: 0;
}
.scooter__btn img {
  width: 100%;
}

@media screen and (min-width: 360px) {
  .scooter__btn {
    width: 75px;
    height: 75px;
    margin-right: calc((100% - 300px)/3);
  }
}
@media screen and (min-width: 1024px) {
  .scooter__main {
    width: 400px;
    height: 400px;
    margin-bottom: 30px;
  }
  .scooter__manage {
    max-width: 464px;
  }
  .scooter__btn {
    width: 80px;
    height: 80px;
    margin-right: calc((100% - 320px)/ 3);
  }
  .scooter__img {
    width: 400px;
  }
  .scooter__img-bl {
    width: 500px;
    height: 500px;
    left: 50%;
    top: 50%;
    transform: translate(-40%, -50%);
  }
  .scooter__img-bl__image {
    width: 450px;
  }
  .scooter__logo {
    width: 144px;
    right: 155px;
  }
}
@media screen and (min-width: 1280px) {
  .scooter__main {
    width: 500px;
    height: 500px;
  }
  .scooter__btn {
    width: 98px;
    height: 98px;
    margin-right: calc((100% - 392px)/3);
  }
  .scooter__img {
    width: 515px;
  }
  .scooter__img--2 {
    top: 47%;
  }
  .scooter__img-bl {
    left: 34%;
    top: 37%;
  }
  .scooter__img-bl__image {
    width: 590px;
  }
  .scooter__logo {
    top: 180px;
    right: 60px;
  }
}
@media screen and (min-width: 1366px) {
  .scooter__manage {
    position: relative;
    left: 15px;
  }
}
.gift {
  padding-bottom: 50px;
}
.gift__content {
  margin-bottom: 64px;
}
.gift__info {
  margin-bottom: 62px;
}
.gift__top {
  margin-bottom: 16px;
}
.gift__title {
  margin-bottom: 24px;
  font-family: CarlsbergSansW05-Bold;
  font-size: 25px;
  line-height: 140%;
}
.gift__how {
  font-family: CarlsbergSansW05-Bold;
