  .gift {
    font-size: 18px;
  }
  .gift__info {
    width: calc(100% - 550px);
  }
  .gift__top {
    margin-bottom: 24px;
  }
  .gift__title {
    margin-bottom: 30px;
    font-size: 30px;
  }
  .gift__how {
    font-size: 22px;
  }
  .gift__text {
    margin-bottom: 48px;
  }
  .gift__text--top {
    padding-bottom: 56px;
  }
