  .gift__top {
    margin-bottom: 26px;
  }
  .gift__info {
    padding-right: 15px;
  }
  .gift__title {
    margin-bottom: 44px;
    font-size: 35px;
  }
  .gift__how {
    font-size: 24px;
  }
  .gift__about {
    padding-right: 8px;
