.gallery {
  padding-top: 10px;
  padding-bottom: 10px;
}
.gallery__inner {
  padding: 22px 20px 10px 20px;
}
.gallery__bottom {
  width: 100%;
}
.gallery__bottom-desk {
  display: none;
}
.gallery__bottom-mob {
  width: 100%;
  display: block;
}
.gallery__top-mob {
  display: block;
  width: 100%;
}
.gallery__top-desk {
  display: none;
}
.gallery__wrapper {
  background: #F4F4F4;
}
.gallery__tabs {
  width: 100%;
  max-width: 335px;
  margin: 0 auto;
  margin-bottom: 32px;
  padding: 2px;
  border-radius: 75px;
  background: #C4C4C4;
  display: flex;
}
.gallery__tabs-btn {
  width: 50%;
  min-height: 43px;
  border-radius: 82px;
  font-size: 18px;
  line-height: 100%;
  font-family: CarlsbergSansW05-Light;
  background: #C4C4C4;
}
.gallery__tabs-btn--active {
  background: #D1112E;
}
.gallery__tabs-btn:hover, .gallery__tabs-btn:focus {
  background: #D1112E;
  color: #fff;
}
.gallery__list {
  margin-bottom: 40px;
}
.gallery__list-sec {
  display: none;
}
.gallery__list-sec--active {
  display: block;
}
.gallery__item {
  margin-bottom: 20px;
}
.gallery__item::last-child {
  margin-bottom: 0;
}
.gallery__img {
  min-height: 222px;
  position: relative;
  overflow: hidden;
}
.gallery__img picture {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.gallery__img picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.gallery__content {
  position: relative;
}
.gallery__content img {
  display: block;
}
.gallery__info {
  padding: 14px 20px;
  position: absolute;
  bottom: 0;
  left: 0;
  color: #fff;
}
.gallery__manage {
  padding: 12px 0;
  font-size: 18px;
  line-height: 120%;
  display: flex;
  justify-content: space-between;
}
.gallery__like-btn {
  display: flex;
  align-items: center;
}
.gallery__like-btn.gallery__like-btn--active {
  color: #D1112E;
}
.gallery__like-btn.gallery__like-btn--active .gallery__like-icon {
  background: transparent;
}
.gallery__like-btn.gallery__like-btn--active .gallery__like-icon::after {
  opacity: 1;
}
.gallery__like-text {
  display: none;
}
.gallery__like-icon {
  display: block;
  width: 24px;
  height: 24px;
  margin-left: 5px;
  position: relative;
  background: url("../images/icons/heart.svg") no-repeat center/contain;
}
.gallery__like-icon::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
